/* Nav Bar  */
nav {
  width: 100vw;
  background-color: #282c34;
}
/* Header inside Nave Bar */
.header {
  height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Links  */
.links {
  color: antiquewhite;
  text-decoration: none;
}
/* Menu Button */
.toggle {
  display: flex;
  position: absolute;
  z-index: 5;
  margin: 10px;
}
/* Close Button */
.toggleclose {
  display: flex;
  position: absolute;
  /* z-index: 5; */
  z-index: 15;
  top: 10px;
  left: 170px;
}
/* Drawer Styles */
.drawer {
  position: absolute;
  background-color: #2b2e2ff4;
  width: 15vw;
  height: 100%;
  display: fixed;
  z-index: 10;
  padding-left: 3%;
  padding-top: 4%;
  color: white;
}
/* Drawer's Links Styles  */
.drawerlinks {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: white;
  text-decoration: none;
  margin-top: 30px;
}
/* Cards */
.wrapper {
  display: flex;
  background-color: #262e30;
  color: white;
  width: 20rem;
  margin-left: 2em;
  margin-bottom: 2em;
  flex-wrap: wrap;
  padding: 12px;
  border-radius: 12px;
}
/* Wrapper of Data */
.users {
  display: flex;
  flex-wrap: wrap;
}
/* Heading Styles */
.Heading {
  text-align: center;
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
}
/* Title Styles */
.title {
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}
/* News style */
.news {
  padding-top: 5%;
  text-transform: capitalize;
}
/*Logged Out */
.wrap,
.loggedout {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
  font-size: 100px;
  letter-spacing: 10px;
  color: red;
}
